<template>
	<view class="box-compents">
		
		<u-tabbar :value="tabVal" @change="uTabbarChange" :fixed="true" :placeholder="true" zIndex="5000"
			:border="false" activeColor="#1989fa" :safeAreaInsetBottom="true">
			<u-tabbar-item text="首页">
				<image class="u-tabbar-item-icon" slot="inactive-icon" src="@/static/images/tabbar/home.png">
				</image>
				<image class="u-tabbar-item-icon" slot="active-icon" src="@/static/images/tabbar/home-active.png">
				</image>
			</u-tabbar-item>
			<u-tabbar-item text="分析">
				<image class="u-tabbar-item-icon" slot="inactive-icon" src="@/static/images/tabbar/category.png">
				</image>
				<image class="u-tabbar-item-icon" slot="active-icon" src="@/static/images/tabbar/category-active.png">
				</image>
			</u-tabbar-item>
			<u-tabbar-item text="报表">
				<image class="u-tabbar-item-icon" slot="inactive-icon" src="@/static/images/tabbar/xiaoshou.png">
				</image>
				<image class="u-tabbar-item-icon" slot="active-icon" src="@/static/images/tabbar/xiaoshou-active.png">
				</image>
			</u-tabbar-item>
			<u-tabbar-item text="我的">
				<image class="u-tabbar-item-icon" slot="inactive-icon" src="@/static/images/tabbar/user.png">
				</image>
				<image class="u-tabbar-item-icon" slot="active-icon" src="@/static/images/tabbar/user-active.png">
				</image>
			</u-tabbar-item>
		</u-tabbar>

	</view>
</template>

<script>
	export default {
		props: {
			tabVal: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {

			};
		},
		mounted() {

		},
		methods: {
			uTabbarChange(index) {
				if (index == 0) {
					uni.navigateTo({
						url: "/pages/home/home",
						animationType: "none"
					})
				} else if (index == 1) {
					uni.navigateTo({
						url: "/pages/analysis/analysis",
						animationType: "none"
					})
				} else if (index == 2) {
					uni.navigateTo({
						url: "/pages/statement/statement",
						animationType: "none"
					})
				} else if (index == 3) {
					uni.navigateTo({
						url: "/pages/my/my",
						animationType: "none"
					})
				}
			},

		},
	}
</script>

<style lang="scss" scoped>
	.box-compents {
		
		/deep/.u-tabbar {
			z-index: 10 !important;
			uni-image{
				width: 18px;
				height: 18px;
				border-radius: 0;
				img {
					width: 18px;
					height: 18px;
				}
			}
		}
	}
</style>
<style lang="scss">
	page {
		// background-image: linear-gradient(to bottom, $my-page-linear-bgcolor, #fff, #fff);
	}
</style>
